.contactme-container {
  margin-top: 20px;
  height: 350px;
  background-color: transparent;
  overflow-y: auto;
}
.contactme-container h1 {
  color: var(--text-color-dark);
}
.contactme-container::-webkit-scrollbar {
  display: none;
}
@media (orientation: portrait) {
  .contactme-container {
    height: 400px;
  }
}
@media (max-height: 300px) {
  .contactme-container {
    height: 400px;
  }
}
