@import url(https://fonts.googleapis.com/css2?family=Saira&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --bg-img: url(/lianelartiles/static/media/hive.42925bb0.svg);
  --bg-light-img: url(/lianelartiles/static/media/light-hive.09c5ffe7.svg);
  --bg-img-portrait: url(/lianelartiles/static/media/hive-portrait.7599f26f.svg);
  --bg-light-img-portrait: url(/lianelartiles/static/media/light-vertical-hive.ad5bdb57.svg);
  --bg-color-dark: #292a2e;
  --bg-color-light: #b8b8b8;
  --text-color-dark: #ffffff;
  --text-color-light: #000000;
  --footer-color-dark: #b8b8b8;
  --footer-color-light: #292a2e;
  --bg-gradient-dark-a: rgba(255, 255, 255, 0.3) 0%;
  --bg-gradient-dark-b: rgba(255, 255, 255, 0) 100%;
  --bg-gradient-light-a: rgba(255, 255, 255, 0.9) 0%;
  --bg-gradient-light-b: rgba(255, 255, 255, 0) 100%;
  --ft-gradient-a: rgba(255, 255, 255, 0.3) 0%;
  --ft-gradient-b: rgba(255, 255, 255, 0) 100%;
}
.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  flex-grow: 1;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
#root {
  background: url(/lianelartiles/static/media/hive.42925bb0.svg),
    linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.3) 0%,
      rgba(255, 255, 255, 0) 100%),
    #292a2e;
  background: var(--bg-img),
    linear-gradient(
      180deg,
      var(--bg-gradient-dark-a),
      var(--bg-gradient-dark-b)
    ),
    var(--bg-color-dark);
  width: 100%;
  background-size: 100% 100%, 100% 100%;
  background-repeat: no-repeat;
  min-height: 100vh;
  padding: 0;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Saira", sans-serif;
  font-size: 1.2rem;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.link {
  display: flex;
  text-decoration: none;
  padding-top: 5px;
  padding-bottom: 5px;
}

@media (orientation: portrait) {
  #root {
    background: url(/lianelartiles/static/media/hive-portrait.7599f26f.svg),
      linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.3) 0%,
        rgba(255, 255, 255, 0) 100%),
      #292a2e;
    background: var(--bg-img-portrait),
      linear-gradient(
        180deg,
        var(--bg-gradient-dark-a),
        var(--bg-gradient-dark-b)
      ),
      var(--bg-color-dark);
    width: 100%;
    background-size: 100% 100%, 100% 100%;
    background-repeat: no-repeat;
    min-height: 100vh;
  }
}
.general-container > {
  display: flex;
  height: 100vh;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

#me {
  text-align: right;
  margin-right: 40%;
  margin-top: 25vh;
  overflow-y: auto;
  height: 75%;
}
#me::-webkit-scrollbar {
  display: none;
}
#las {
  font-size: 8vw;
}
#dev {
  font-size: 3vw;
}
#lema {
  font-size: 2vw;
}
@media (max-width: 1200px) {
  #me {
    text-align: right;
    margin-right: 35vw;
    margin-top: 25vh;
  }
}
@media (max-width: 800px) {
  #me {
    margin-top: 25vh;
    margin-right: 0%;
    text-align: center;
  }
  #las {
    font-size: 10vw;
  }
  #dev {
    font-size: 5vw;
  }
  #lema {
    font-size: 3.5vw;
  }
}
@media (max-width: 500px) {
  #me {
    margin-top: 30%;
    margin-bottom: 10px;
    width: 100vw;
  }
}
@media (max-width: 370px) {
  #me {
    margin: auto;
    margin-top: 15%;
    text-align: center;
  }
}
@media (max-height: 450px) {
  #me {
    margin: auto;
    margin-top: 0%;
    text-align: center;
  }
}
@media (orientation: portrait) and (min-height: 350px) {
  #me {
    height: 400;
    overflow-y: auto;
  }
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: auto;
  padding-left: 5%;
  padding-right: 5%;
  margin-top: 10px;
}
.logo {
  display: flex;
  align-items: center;
  margin: auto;
  position: relative;
  width: 100px;
  height: 100px;
  -webkit-filter: drop-shadow(7px 7px 7px rgba(0, 0, 0, 1));
          filter: drop-shadow(7px 7px 7px rgba(0, 0, 0, 1));
  transition: transform 1s;
}
.logo:hover {
  transform: rotateZ(360deg) scale(0.8, 0.8);
  transition: transform 1s;
}
.header ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-filter: drop-shadow(6px 6px 4px rgba(0, 0, 0, 0.6));
          filter: drop-shadow(6px 6px 4px rgba(0, 0, 0, 0.6));
  /* border: solid 1px black; */
}
.header span {
  padding: 10px;
}
.hlink {
  position: relative;
  padding-top: 5px;
  padding-bottom: 5px;
}
.hlink::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #fff;
  position: absolute;
  left: 0;
  bottom: 0px;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.5s ease;
}
.hlink:hover::after {
  transform: scaleX(1);
}

@media screen and (max-width: 800px) {
  .logo {
    margin: auto;
    margin-top: 10px;
  }
  .main-header {
    flex-direction: row;
    margin: auto;
    margin-top: 10px;
  }
}
@media (max-width: 500px) {
  .logo {
    margin: auto;
    margin-top: 10px;
  }
  .main-header {
    flex-direction: column;
    margin: auto;
  }
}
@media (max-width: 370px) {
  .main-header {
    flex-direction: row;
  }
  .header ul {
    flex-direction: column;
    text-align: center;
  }
  .logo {
    margin: auto;
    margin-top: 15px;
  }
  .header {
    margin-top: 5px;
  }
  .header span {
    padding: 0px;
    margin: 0%;
  }
}

.main-footer {
  display: flex;
  font-weight: normal;
  position: fixed;
  bottom: 0;
  left: 0;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 50px;
  background: linear-gradient(
      180deg,
      var(--ft-gradient-a),
      var(--ft-gradient-b)
    ),
    var(--footer-color-dark);
}
#copyright {
  position: fixed;
  bottom: 50px;
  padding: 0;
  right: 0;
  margin: 0;
  font-size: 10px;
  padding-right: 5px;
  text-align: end;
}
.footer {
  display: flex;
  list-style: none;
  flex-direction: row;
  margin: 0;
  padding: 0;
}
.footer .flink {
  -webkit-filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.9));
          filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.9));
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 10px;
  padding-right: 10px;
}
.flags {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.flags img {
  height: 20px;
  width: 30px;
  -webkit-filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.5));
}
.toggle {
  height: 50px;
  width: 50px;
}
#toggle {
  -webkit-filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.5));
  height: 50px;
  width: 50px;
}
#toggle:hover {
  transform: rotateY(180deg);
}
.flink:hover {
  background-color: rgba(0, 0, 0, 0.281);
  border-radius: 10px;
}
#esp:hover,
#eng:hover {
  transform: scale(1.4, 1.4);
  transition: transform 1s;
}
@media (min-height: 1000px) {
  .main-footer {
    height: 75px;
  }
}
@media (max-width: 800px) {
  .toggle {
    justify-content: space-around;
  }
}
@media (max-width: 500px) {
  .footer .flink {
    padding-top: 8px;
    padding-bottom: 8px;
  }
  .footer {
    flex-direction: row;
  }
  .footer span {
    padding: 0%;
    margin: auto;
  }
}
@media (max-width: 370px) {
  .footer .flink {
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .footer {
    flex-direction: column;
  }
  .main-footer {
    height: 75px;
  }
  #copyright {
    bottom: 75px;
  }
}
@media (max-height: 450px) {
  #copyright {
    bottom: 50px;
  }
  .main-footer {
    height: 50px;
  }
}

.check {
  color: green;
}

